.dialog_item a {
    display: block;
    width: 100%;
    min-height: 60px;
    padding: 5px;
    text-decoration: none;
    box-sizing: border-box;
    border-radius: 30px;
    color: black;
}

.activeLink {
    cursor: default;
    background-color: rgb(221, 221, 221);
}

.dialog_item img {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    float: left;
    margin-right: 10px;
}

.dialog_item p {
    margin: 0;
}